import { useState } from 'react'
import Title from '../components/Title'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import * as Icon from 'react-bootstrap-icons';
import withReactContent from 'sweetalert2-react-content';
import Swal from 'sweetalert2';
import { useForm } from "react-hook-form";



const About = () => {

  const MySwal = withReactContent(Swal)

  const { register, handleSubmit, reset, formState: { errors } } = useForm({
    defaultValue:{}
  });
  const onError = (errors, e) => console.log(errors, e);

  const [formData, setFormData] = useState({})

  const Toast = Swal.mixin({
    toast: true,
    position: 'top-end',
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener('mouseenter', Swal.stopTimer)
      toast.addEventListener('mouseleave', Swal.resumeTimer)
    }
  })

  const onSubmit = (data) => {
    // alert(JSON.stringify(data))
    // MySwal.fire({
    //   title: <strong></strong>,
    //   html: <i>{data.userName}<br/>
    //   </i>,
    //   icon: 'success',
    // })
    Toast.fire({
      icon: 'success',
      title: '',
      html: `${data.userName}<br/>
      `,
    })
    setFormData(data)
    reset();
  };
  console.log(formData)





  return (
    <>
      <div className='container py-5' style={{ maxWidth: "800px" }}>
        <Title cn="" jp="" />
        {/*  */}
        <Form className='d-flex flex-column ps-5 mb-5' onSubmit={handleSubmit(onSubmit, onError)}>
          <Form.Group className="mb-3" controlId="formBasicName" >
            <Form.Floating>
              <Form.Control
                type="text"
                name="userName"
                placeholder=" / "
                {...register("userName", {
                  required: { value: true, message: "" }
                })}
              />
              <Form.Text className='text-muted'>
                {errors.userName?.message}
              </Form.Text>
              <label htmlFor="floatingInputCustom">
                 / 
              </label>
            </Form.Floating>
          </Form.Group>
          {/* -------------- */}
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Floating>
              <Form.Control
                type="text"
                name="email"
                placeholder=""
                {...register("email", {
                  required: { value: true, message: "email" },
                  pattern: { value: /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g, message: "email" }
                })}
              />
              <Form.Text className="text-muted">
                {errors.email?.message}
              </Form.Text>
              <label htmlFor="floatingInputCustom">
                
              </label>
            </Form.Floating>
          </Form.Group>
          {/* -------------- */}
          <Form.Group className="mb-3" controlId="formBasicTitle" >
            <Form.Floating>
              <Form.Control
                type="text"
                name="title"
                placeholder=""
              />
              <label htmlFor="floatingInputCustom"></label>
            </Form.Floating>
          </Form.Group>
          {/* -------------- */}
          <Form.Group className="mb-3" controlId="formBasicMessage" >
            <Form.Floating>
              <Form.Control
                as="textarea"
                style={{ minHeight: "180px" }}
                name="message"
                placeholder=""
                {...register("message", {
                  required: { value: true, message: "" }
                })}
              />
              <Form.Text>
                {errors.message?.message}
              </Form.Text>
              <label htmlFor="floatingInputCustom">
                
              </label>
            </Form.Floating>
          </Form.Group>
          {/* -------------- */}
          <Button variant="primary" type="submit" style={{ width: "100px" }} className='align-self-end' >
            <span className='pe-1'></span>
            <Icon.ArrowRightShort size={25} />
          </Button>
        </Form>
        {/*  */}
        <section className='container py-5'>

          <div className="row justify-content-between">

            <div className="col-12 col-md-6 py-5 px-4 mb-5 bg-light">
              <h3 className='fw-bold text-primary mb-2'></h3>
              <p className='mb-5'>
                <br />
                <br />
                <br />
                <br />
                <br />
                
              </p>
              <h3 className='fw-bold text-primary mb-2'></h3>
              <ul>
                <li>
                  <Icon.BrightnessLowFill size={20} className="me-2" />
                  
                </li>
                <li>
                  <Icon.BrightnessLowFill size={20} className="me-2" />
                  
                </li>
                <li>
                  <Icon.BrightnessLowFill size={20} className="me-2" />
                  
                </li>
                <li>
                  <Icon.BrightnessLowFill size={20} className="me-2" />
                  Sakenote
                </li>
                <li>
                  <Icon.BrightnessLowFill size={20} className="me-2" />
                  hex school 2022 17
                </li>
                <li>
                  <Icon.BrightnessLowFill size={20} className="me-2" />
                  hex school 
                </li>
              </ul>
            </div>

            <div className="col-12 col-md-5 py-5 px-4 mb-5 bg-dark text-white col-4 d-flex flex-column justify-content-center">
              <h3 className='fw-bold mb-2'></h3>
              <p className='fw-lighter'>
                AKI CHENG<br />
                <br />
                <br />
                <br />
                <h5></h5>
                <ul>
                  <li className='mb-2'>
                    <Icon.Mailbox size={20} className="me-2" />
                    mailahsi.aki@gmail.com
                  </li>
                  <li className='mb-2'>
                    <Icon.Telegram size={20} className="me-2" />
                    telegramahsiaki
                  </li>
                  <li className='mb-2'>
                    <Icon.Line size={20} className="me-2" />
                    LINEsakeholic
                  </li>
                </ul>
              </p>
            </div>

          </div>
        </section>

      </div>
    </>
  )
}

export default About